export default {
  data: function data() {
    return {
      sec: 10,
      setting: null,
      code: {
        header: [],
        footer: []
      }
    };
  },
  created: function created() {
    var _this = this;

    this.setting = localStorage.getItem("c_shopConfigJson") ? JSON.parse(localStorage.getItem("c_shopConfigJson")) : null;
    if (!this.setting || !this.setting.customCode || !this.setting.customCode.length) return;
    this.setting.customCode.forEach(function (item) {
      if (item.pages.includes('404页面')) {
        if (item.position == '底部') {
          _this.code.footer.push(item.desc);
        }

        if (item.position == '顶部') {
          _this.code.header.push(item.desc);
        }
      }
    });
  },
  mounted: function mounted() {
    var _this2 = this;

    var t = setInterval(function () {
      _this2.sec--;

      if (_this2.sec < 1) {
        clearInterval(t);

        _this2.gohome();
      }
    }, 1000); // 404埋点

    this.$Burying({
      object: '12002'
    });
  },
  methods: {
    gohome: function gohome() {
      window.location.href = '/';
    }
  }
};